@charset "utf-8";

main{width: 1200px; margin: 0 auto;}
main > form{width: 600px; margin: 0 auto;}
label{font-size: 16px;}
input[type=text]{width: 500px; padding: 10px; border:2px solid #f00; margin: 10px 0px;}
.text_btn{width: 525px; padding: 20px; font-size: 20px; background: rgb(240, 80, 80); color: #fff; border:none; margin-top: 30px;
transition: 0.3s; border-radius: 5px;}
.movie_btn{width: 200px; padding: 20px 0px; font-size: 20px; transition: 0.3s;
color: #fff; background: rgb(240, 80, 80); border:none;margin: 10px 0px; border-bottom: 3px solid #f00; border-radius: 10px;}
.text_btn:hover, .movie_btn:hover{background:rgb(228, 43, 43);}
.movie{
  display: flex;
  justify-content: space-evenly;
  width: 1200px;
  flex-wrap: wrap;
  margin-top: 50px;
  white-space: normal;
  overflow-wrap: break-word;
}
.movie li{width: 300px;}
.movie p{line-height: 30px;}
.movie_title{
  font-weight: bold;
  font-size: 18px;
  width: 200px;
}
/* 이미지서식 */
.poster_box{
  width: 200px; height: 285px;
  overflow: hidden;
}
.poster{
  width: 200px;
  border-radius: 5px;
  transition: 0.3s;
}
.poster:hover{
  transform: scale(1.1);
}

